___CSS_LOADER_EXPORT___.push([module.id, `:root {
  --black: #002;
  --white: #fff;
  --light-gray: #d3d3d3;
  --dark-gray: #f3f0f0;
}

.navbar {
  justify-content: space-between;
  width: 90%;
  margin: auto;
}

.navbar .navlinks {
  gap: 1rem;
  cursor: pointer;
}

.book-section {
  margin: 20px;
}

.book-section h1 {
  font-size: 2.5rem;
  margin: auto;
}

.horizontal-line {
  max-width: 100px;
  height: 4px;
  background: var(--black);
  margin: 10px auto;
  border-radius: 798px;
}

.book-section .added-books {
  max-width: 500px;
  max-height: 300px;
  border: 4px solid var(--black);

  /* overflow-y: scroll; */
  border-radius: 8px;
  text-align: center;
}

.added-books .details {
  justify-content: space-between;
  margin: 0;
  padding: 8px;
}

.added-books .book:nth-child(even) {
  background: var(--light-gray);
}

.added-books .book:nth-child(odd) {
  background: var(--dark-gray);
}

.red {
  color: red;
  text-transform: capitalize;
}

.delete-btn,
.submit-btn {
  background: transparent;
  border: 1px solid var(--black);
  box-shadow: 2px 3px 5px var(--black);
  padding: 4px 8px;
  border-radius: 4px;
}

.book-title,
.author {
  text-transform: capitalize;
  font-family: 'Poppins', sans-serif;
  font-weight: 400;
}

.add-new-book {
  text-align: center;
}

.add-new-book .new-book-list {
  font-weight: 600;
  font-size: 1.8rem;
  text-transform: capitalize;
  margin: auto;
}

.form {
  margin: 10px auto;
  display: flex;
  flex-direction: column;
  gap: 10px;
  max-width: 300px;
}

.form .submit-btn {
  width: 50px;
  justify-self: end;
  align-self: flex-end;
}

.inputs {
  color: var(--black);
  border: 2px solid var(--black);
  border-radius: 4px;
  padding: 4px;
  font-weight: 600;
  width: 100%;
}

.nav-wrapper {
  border: 4px solid var(--black);
  padding: 8px;
  border-radius: 8px;
  margin: 10px auto;
}

.footer-container {
  position: fixed;
  left: 0;
  bottom: 10px;
  width: 100%;
}
